/**
 * 
 *
 * :
 * 1. name(path)
 */
import Vue from 'vue'
import Router from 'vue-router'
import http from '@/utils/httpRequest'
import { isURL } from '@/utils/validate'
import { clearLoginInfo } from '@/utils'

Vue.use(Router)

// , webpack, 
const _import = require('./import-' + process.env.NODE_ENV)

// ()
const globalRoutes = [
    { path: '/404', component: _import('common/404'), name: '404', meta: { title: '404' } },
    { path: '/login', component: _import('common/login'), name: 'login', meta: { title: '' } },
    // { path: '/menuLeftHome', component: _import('home/menuLeftHome'), name: 'menuLeftHome', meta: { title: 'menuLeftHome' } },
    // { path: '/userMain', component: _import('home/userMain'), name: 'userMain', meta: { title: 'userMain' } },
    // { path: '/welcome', component: _import('home/welcome'), name: 'welcome', meta: { title: 'welcome' } },
]

// ()
const mainRoutes = {
    path: '/',
    component: _import('main'),
    name: 'main',
    redirect: { name: 'home' },
    meta: { title: '' },
    children: [
        // meta
        // 1. isTab: tab, true: , false: 
        // 2. iframeUrl: iframe, 'http[s]://': , '': 
        // : iframe, tab, iframe!
        { path: '/home', component: _import('common/home'), name: 'home', meta: { title: '' } },
        { path: '/theme', component: _import('common/theme'), name: 'theme', meta: { title: '' } },
        { path: '/demo-echarts', component: _import('demo/echarts'), name: 'demo-echarts', meta: { title: 'demo-echarts', isTab: true } },
        { path: '/demo-ueditor', component: _import('demo/ueditor'), name: 'demo-ueditor', meta: { title: 'demo-ueditor', isTab: true } }
    ],
    beforeEnter (to, from, next) {
        let token = Vue.cookie.get('token')
        if (!token || !/\S/.test(token)) {
            clearLoginInfo()
            next({ name: 'login' })
        }
        next()
    }
}

const router = new Router({
    mode: 'hash',
    scrollBehavior: () => ({ y: 0 }),
    isAddDynamicMenuRoutes: false, // ()
    routes: globalRoutes.concat(mainRoutes)
})

router.beforeEach((to, from, next) => {
    // ()
    // 1.  or , 
    // 2. , 
    if (router.options.isAddDynamicMenuRoutes || fnCurrentRouteType(to, globalRoutes) === 'global') {
        next()
    } else {
        http({
            url: http.adornUrl('/sys/menu/nav'),
            method: 'get',
            params: http.adornParams()
        }).then(({data}) => {
            if (data && data.code === 0) {
                fnAddDynamicMenuRoutes(data.menuList)
                router.options.isAddDynamicMenuRoutes = true
                sessionStorage.setItem('menuList', JSON.stringify(data.menuList || '[]'))
                sessionStorage.setItem('permissions', JSON.stringify(data.permissions || '[]'))
                next({ ...to, replace: true })
            } else {
                sessionStorage.setItem('menuList', '[]')
                sessionStorage.setItem('permissions', '[]')
                next()
            }
        }).catch((e) => {
            console.log(`%c${e} `, 'color:blue')
            router.push({ name: 'login' })
        })
    }
})

/**
 * , global: , main: 
 * @param {*} route 
 */
function fnCurrentRouteType (route, globalRoutes = []) {
    var temp = []
    for (var i = 0; i < globalRoutes.length; i++) {
        if (route.path === globalRoutes[i].path) {
            return 'global'
        } else if (globalRoutes[i].children && globalRoutes[i].children.length >= 1) {
            temp = temp.concat(globalRoutes[i].children)
        }
    }
    return temp.length >= 1 ? fnCurrentRouteType(route, temp) : 'main'
}

/**
 * ()
 * @param {*} menuList 
 * @param {*} routes ()
 */
function fnAddDynamicMenuRoutes (menuList = [], routes = []) {
    var temp = []
    for (var i = 0; i < menuList.length; i++) {
        if (menuList[i].list && menuList[i].list.length >= 1) {
            temp = temp.concat(menuList[i].list)
        } else if (menuList[i].url && /\S/.test(menuList[i].url)) {
            menuList[i].url = menuList[i].url.replace(/^\//, '')
            var route = {
                path: menuList[i].url.replace('/', '-'),
                component: null,
                name: menuList[i].url.replace('/', '-'),
                meta: {
                    menuId: menuList[i].menuId,
                    title: menuList[i].name,
                    isDynamic: true,
                    isTab: true,
                    iframeUrl: ''
                }
            }
            // urlhttp[s]://, iframe
            if (isURL(menuList[i].url)) {
                route['path'] = `i-${menuList[i].menuId}`
                route['name'] = `i-${menuList[i].menuId}`
                route['meta']['iframeUrl'] = menuList[i].url
            } else {
                try {
                    route['component'] = _import(`modules/${menuList[i].url}`) || null
                } catch (e) {}
            }
            routes.push(route)
        }
    }
    if (temp.length >= 1) {
        fnAddDynamicMenuRoutes(temp, routes)
    } else {
        mainRoutes.name = 'main-dynamic'
        mainRoutes.children = routes
        router.addRoutes([
            mainRoutes,
            { path: '*', redirect: { name: '404' } }
        ])
        sessionStorage.setItem('dynamicMenuRoutes', JSON.stringify(mainRoutes.children || '[]'))
        console.log('\n')
        console.log('%c!<-------------------- () s -------------------->', 'color:blue')
        console.log(mainRoutes.children)
        console.log('%c!<-------------------- () e -------------------->', 'color:blue')
    }
}

export default router
