/**
 * , SVG Sprite(http://www.iconfont.cn/
 *
 * :
 *  1. , (, )
 *  2-1. icon, icon,  ->  -> SVG -> ()
 *  2-2. icons, [iconfont.js], [./iconfont.js]
 *  3.  [<icon-svg name="canyin"></icon-svg>]
 *
 * :
 *  1. 2-2 icons, getNameList
 */
import Vue from 'vue'
import IconSvg from '@/components/icon-svg'
import './iconfont.js'

Vue.component('IconSvg', IconSvg)

const svgFiles = require.context('./svg', true, /\.svg$/)
const iconList = svgFiles.keys().map(item => svgFiles(item))

export default {
  // icon-(*).svg, [shouye, xitong, zhedie, ...]
  getNameList () {
    return iconList.map(item => item.default.id.split('-')[1])
  }
}
