import Vue from 'vue'
import axios from 'axios'
import router from '@/router'
import qs from 'qs'
import merge from 'lodash/merge'
import { clearLoginInfo } from '@/utils'

const http = axios.create({
  timeout: 1000 * 30,
  withCredentials: true,
  headers: {
    'Content-Type': 'application/json; charset=utf-8'
  }
})

/**
 * 
 */
http.interceptors.request.use(config => {
  config.headers['token'] = Vue.cookie.get('token') // token
  return config
}, error => {
  return Promise.reject(error)
})

/**
 * 
 */
http.interceptors.response.use(response => {

    if(response.data){
        var resCode = response.data.code
        if (0 === resCode) {  //code0
            return response

        } else if (resCode === 401) { // 401, token, 
            clearLoginInfo()
            router.push({ name: 'login' })

        } else { //500, 404...
            var resMsg = response.data.msg
            if(resMsg){
              Vue.prototype.$message.error(resMsg)
            }
        }
    }

    return response
}, error => {
  return Promise.reject(error)
})

/**
 * 
 * @param {*} actionName action
 */
http.adornUrl = (actionName) => {
  //  && , [/proxyApi/]!
  // return (process.env.NODE_ENV !== 'production' && process.env.OPEN_PROXY ? '/proxyApi/' : window.SITE_CONFIG.baseUrl) + actionName
  // return '/proxyApi' + actionName
  return process.env.VUE_APP_PROXY_TARGET + actionName
}

/**
 * get
 * @param {*} params 
 * @param {*} openDefultParams ?
 */
http.adornParams = (params = {}, openDefultParams = true) => {
  var defaults = {
    't': new Date().getTime()
  }
  return openDefultParams ? merge(defaults, params) : params
}

/**
 * post
 * @param {*} data 
 * @param {*} openDefultdata ?
 * @param {*} contentType 
 *  json: 'application/json; charset=utf-8'
 *  form: 'application/x-www-form-urlencoded; charset=utf-8'
 */
http.adornData = (data = {}, openDefultdata = true, contentType = 'json') => {
  var defaults = {
    't': new Date().getTime()
  }
  data = openDefultdata ? merge(defaults, data) : data
  return contentType === 'json' ? JSON.stringify(data) : qs.stringify(data)
}

export default http
